<script>
export default {
  props: {
    value: {
      type: String,
    },
    name: {
      type: String,
    },
    title: {
      type: String,
    },
    textarea: {
      type: Boolean,
      default: false,
    },
    error: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return { innerValue: this.value };
  },
  watch: {
    innerValue(newValue) {
      const { name } = this;
      this.$emit('onChange', name, newValue);
    },
  },
};
</script>

<template>
  <m-text-field
    outlined
    v-model="innerValue"
    :style="{'width': '100%', 'height': textarea ? '300px':'auto'}"
    :id="name"
    :dense="!textarea"
    :textarea="textarea"
    :useNativeValidation="false"
    :valid="!error">
    <m-floating-label :for="name">
      {{ title }}
    </m-floating-label>
    <m-line-ripple slot="bottomLine"/>
  </m-text-field>
</template>
