import './init-mui-components';
import { action } from '@storybook/addon-actions';
import Editor from '../components/Editor';


const DEFAULT_GLOBAL_OPTIONS = {
  baseUrl: process.env.STORYBOOK_BASE_URL || 'https://api.kloudless.com',
  mapperPath: (process.env.STORYBOOK_MAPPER_PATH ||
    'https://static-cdn.kloudless.com/p/platform/mapper/index.html'),
};

const DEFAULT_LAUNCH_OPTIONS = {
  appId: process.env.STORYBOOK_KLOUDLESS_APP_ID || '',
  token: process.env.STORYBOOK_KLOUDLESS_ACCOUNT_TOKEN || '',
  mode: 'modal',
  element: '.dm',
  title: 'Data Mapper!!',
  kloudlessObjectName: 'contact',
  kloudlessObjectFriendlyName: 'My Contact',
  kloudlessObjectFields: [
    { name: 'id', friendlyName: 'ID', description: 'Contact ID' },
    { name: 'name' },
    { name: 'phone', friendlyName: 'Phone' },
  ],
  serviceObjectName: '',
};

export default function createStory() {
  return {
    components: { Editor },
    data() {
      return {
        globalOptions: { ...DEFAULT_GLOBAL_OPTIONS },
        launchOptions: JSON.stringify(DEFAULT_LAUNCH_OPTIONS, null, 2),
      };
    },
    computed: {
      jsonLaunchOptions() {
        try {
          return JSON.parse(this.launchOptions);
        } catch (ex) {
          return null;
        }
      },
      error() {
        const result = {
          launchOptions: false,
          globalOptions: { baseUrl: false, mapperPath: false },
        };
        const { globalOptions: { baseUrl, mapperPath } } = this;
        if (!baseUrl) {
          result.globalOptions.baseUrl = true;
        }
        if (!mapperPath) {
          result.globalOptions.mapperPath = true;
        }
        if (this.jsonLaunchOptions === null) {
          result.launchOptions = true;
        } else {
          const { appId, token, kloudlessObjectName } = this.jsonLaunchOptions;
          if (!appId || !token || !kloudlessObjectName) {
            result.launchOptions = true;
          }
        }
        return result;
      },
      valid() {
        const {
          launchOptions,
          globalOptions: { baseUrl, mapperPath },
        } = this.error;
        return !launchOptions && !baseUrl && !mapperPath;
      },
    },
    beforeDestroy() {
      this.destroyMapper();
    },
    methods: {
      onLaunchOptionChange(value) {
        this.launchOptions = value;
      },
      onGlobalOptionChange(name, value) {
        this.globalOptions[name] = value;
      },
      destroyMapper() {
        if (this.mapper) {
          this.mapper.destroy();
        }
      },
      bindEventHandlers() {
        ['open', 'close', 'destroyed', 'success', 'error'].forEach((event) => {
          this.mapper.on(event, action(event));
        });
      },
      onLaunch() {
        if (this.valid) {
          this.destroyMapper();
          window.Kloudless.mapper.setOptions(this.globalOptions);
          this.mapper = new window.Kloudless.mapper();
          this.bindEventHandlers();
          this.mapper.launch(this.jsonLaunchOptions);
          this.launchOptions = JSON.stringify(this.jsonLaunchOptions, null, 2);
        }
      },
    },
    template: `
      <div>
        <editor
          @onLaunchOptionChange="onLaunchOptionChange"
          @onGlobalOptionChange="onGlobalOptionChange"
          @onLaunch="onLaunch"
          :error="error"
          :disabled="!valid"
          :launchOptions="launchOptions"
          :globalOptions="globalOptions">
        </editor>
        <div class="dm"></div>
      </div>`,
  };
}
