<script>
import TextField from '../components/TextField';

export default {
  components: { TextField },
  props: {
    launchOptions: {
      type: String,
    },
    globalOptions: {
      type: Object,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
    error: {
      type: Object,
    },
  },
  methods: {
    onClick(e) {
      e.preventDefault();
      this.$emit('onLaunch');
    },
    onLaunchOptionChange(name, value) {
      this.$emit('onLaunchOptionChange', value);
    },
    onGlobalOptionChange(name, value) {
      this.$emit('onGlobalOptionChange', name, value);
    },
  },
};
</script>

<template>
  <m-card outlined>
    <form @submit="onClick">
      <h3>
        Global Options
      </h3>
      <m-layout-grid>
        <m-layout-grid-inner>
          <m-layout-grid-cell :span="10">
            <text-field
              @onChange="onGlobalOptionChange"
              :value="globalOptions.baseUrl"
              title="Kloudless API Server Base URL *"
              name="baseUrl"
              :error="error.globalOptions.baseUrl">
            </text-field>
          </m-layout-grid-cell>
          <m-layout-grid-cell :span="10">
            <text-field
              @onChange="onGlobalOptionChange"
              :value="globalOptions.mapperPath"
              title="Mapper Path *"
              name="mapperPath"
              :error="error.globalOptions.mapperPath">
            </text-field>
          </m-layout-grid-cell>
        </m-layout-grid-inner>
      </m-layout-grid>
      <h3>
        Launch Options
      </h3>
      <m-layout-grid>
        <m-layout-grid-inner>
          <m-layout-grid-cell :span="10">
            <text-field
              @onChange="onLaunchOptionChange"
              :value="launchOptions"
              :textarea="true"
              title="Launch Options"
              name="launchOptions"
              :error="error.launchOptions">
            </text-field>
          </m-layout-grid-cell>
          <m-layout-grid-cell :span="10">
            <m-button outlined @click="onClick" :disabled="disabled">
              Launch
            </m-button>
          </m-layout-grid-cell>
        </m-layout-grid-inner>
      </m-layout-grid>
    </form>
  </m-card>
</template>
